[data-page-type="special_deals_page"]{

  .content_block.title_container{
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    padding: 24px 104px 32px 104px;
  }

  .breadcrumbs_wrapper{
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
  }

  .breadcrumbs{
    margin: 0 4px 0 0;
  }

  h1{
    padding: 16px 0 0 0;
    text-align: center;
  }

  .content_block.deal_wpapper{
    padding: 0 104px 48px 104px;

    img{
      display: block;
      width: 100%;
      height: auto;
    }
  }

  .deal_content{
    padding: 24px 0 0 0;

    &,
    p,
    span{
      line-height: 26px;
      letter-spacing: 0.01em;
    }
  }

  @include for-pad{

    .content_block.title_container{
      padding: 24px 16px 24px 16px;
    }

    .content_block.deal_wpapper{
      padding: 0 16px 40px 16px;
    }

    .content_block{
      width: 736px;
    }
  }

  @include for-mobile{

    .content_block.title_container{
      padding: 24px 16px 24px 16px;
    }

    h1{
      padding: 8px 0 0 0;
    }

    .content_block.deal_wpapper{
      padding: 0 16px 32px 16px;
    }

    .deal_content{
      padding: 16px 0 0 0;
    }

  }
}