[data-page-type="review_form"]{
  position: relative;
  display: flex;
  justify-content: center;
  padding: 0 0 48px 0;
  
  form{
    position: relative;
    width: 1016px;
    padding: 24px 48px 40px 48px;
    background: #FFF9F0;
    border-radius: 8px;
  }

  h2{
    padding: 0 0 12px 0;
    color: $headerBlack;
  }

  .close_icon_mobile{
    display: none;
  }

  form > p{
    line-height: 24px;
    letter-spacing: 0.01em;
  }

  .form_fields_wrapper{
    display: flex;
    flex-flow: row wrap;
    padding: 16px 0 8px 0;
  }

  input,
  textarea{
    transition: 0.3s; 
    background: #ffffff;
    border: 1px solid #DADEE1;
    box-shadow: inset 0px 2px 2px rgba(118, 125, 134, 0.1);
    border-radius: 4px;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.02em;
  }

  input{
    padding: 20px 8px 8px 8px;
  }

  input[name="name"]{
    width: 288px;
    margin: 0 16px 16px 0;
  }

  input[name="age"]{
    width: 104px;
  }

  .textarea_wrapper{
    width: 100%;
  }

  textarea{
    width: 100%;
    min-height: 160px;
    margin: 0 0 16px 0;
    padding: 14px 8px 14px 8px;
    resize: none;
  }

  .submit_wrapper{
    display: flex;
    flex-flow: row nowrap;
    align-items: center;

    ._button{
      padding: 12px 76px 12px 76px;
      margin: 0 16px 0 0;
    }
    
    p,
    a{
      font-size: 12px;
      line-height: 18px;
      letter-spacing: 0.02em;
    }
  }

  .input_wrapper{
    position: relative;

    span{
      position: absolute;
      bottom: 32px;
      left: 8px;
      opacity: 0.6;
      visibility: hidden;
      transition: 0.3s;
      font-size: 14px;
      line-height: 20px;
      letter-spacing: 0.02em;
    }

    input:placeholder-shown ~ span {
      visibility: visible;
    }

    input:placeholder-shown:focus ~ span,
    input:not(:placeholder-shown) ~ span {
      bottom: 46px;
      opacity: 1;
      visibility: visible;
      font-size: 10px;
      line-height: 14px;
    }

  }
  
  .review_form_successful_send{
    position: absolute;
    top: 0;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    width: 1014px;
    height: 100%;
    padding: 48px 207px 48px 207px;
    background: #FFFFFF;
    border-radius: 8px;

    h3{
      padding: 0 0 12px 0;
      color: $headerBlack;
    }

    p{
      line-height: 24px;
      letter-spacing: 0.01em;
      text-align: center;
    }

    .close_icon,
    .close_icon_mobile{
      display: block;
    }
  }

  @include for-pad{
    padding: 160px 16px 40px 16px;

    form{
      width: 736px;
      padding: 24px 24px 32px 24px;
    }

    .review_form_successful_send{
      width: 736px;
      height: calc(100% - 200px);
      margin: 160px 0 0 0;
      padding: 40px 16px 32px 16px;
    }
  }

  @include for-mobile{
    padding: 0 0 32px 0;

    form{
      width: 100%;
      padding: 16px 16px 24px 16px;
    }

    h2{
      padding: 0 0 8px 0;
    }

    .form_fields_wrapper{
      padding: 16px 0 16px 0;
    }

    .input_wrapper{
      width: 100%;
    }
  
    input[name="name"],
    input[name="age"]{
      width: 100%;
      margin: 0 0 16px 0;
    }

    .submit_wrapper{
      flex-flow: column nowrap;

      ._button{
        width: 100%;
        margin: 0 0 8px 0;
        padding: 12px 0 12px 0;
      }
    }

    .review_form_successful_send{
      width: 100%;
      padding: 40px 12px 32px 12px;
      height: auto;

      h3{
        text-align: center;
        padding: 0 0 0 0;
      }
    }
  }
}